import React from 'react';
import {Switch,Route,Redirect} from 'react-router-dom';
import { Header } from '../components/Header';
import { Home } from '../components/Home';
import { About } from '../components/About';
import { Contact } from '../components/Contact';
import { withRouter } from 'react-router-dom';

class RouteDemo extends React.Component{

    constructor(props){
        super(props);
    }

    redirect(){
        this.props.history.replace('/contact/:number');
    }

    render(){
        return(
            <div>
                <Header/>
                {/* <button onClick={this.redirect.bind(this)} className="btn btn-primary">To Contact</button> */}
                <Switch>
                    <Route path="/" component={ Home } exact/>
                    <Route path="/about" component={ About }/>
                    {/* <Redirect from="/ab" to="/about" component={ About } /> */}
                    <Route path="/contact/:number" component={ Contact }/>
                </Switch>
            </div>
        );
    }
}

export default withRouter(RouteDemo);